    background-image: linear-gradient(#394B59 2px, transparent 2px), linear-gradient(90deg, #394B59 0px, transparent 1px), linear-gradient(rgba(255, 255, 255, 0.05) 0px, transparent 1px), linear-gradient(90deg, rgba(255, 255, 255, 0.05) 0px, transparent 1px);
